import { coerceBooleanProperty } from '@angular/cdk/coercion';
import { ChangeDetectionStrategy, Component, Input, OnDestroy, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { Subscription } from 'rxjs';

import { NxTabComponent } from './tab';
import { NxTabGroupBase } from './tab-group-base';
import { NxTabGroupComponent } from './tab-group';

/** @docs-private */
@Component({
  selector: 'nx-tab-body',
  templateUrl: 'tab-body.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./tab-body.scss']
})
export class NxTabBodyComponent implements OnInit, OnDestroy {
  private _appearanceSubscription: Subscription;

  @ViewChild('outlet', { static: true, read: ViewContainerRef }) _outlet: ViewContainerRef;

  @Input() tab: NxTabComponent;

  private _active: boolean = false;

  @Input()
  set active(value: boolean) {
    this._active = coerceBooleanProperty(value);
    if (this._active) {
      this.attach();
    } else {
      this.detach();
    }
  }
  get active(): boolean {
    return this._active;
  }

  constructor(
    private _tabGroup: NxTabGroupBase) { }

  ngOnInit() {
    this._appearanceSubscription = (this._tabGroup as NxTabGroupComponent)._appearanceChange.subscribe(() => {
      this.detach();
    });
  }

  ngOnDestroy() {
    this._appearanceSubscription.unsubscribe();
  }

  attach() {
    this._outlet.insert(this.tab.contentViewRef);
  }

  detach() {
    const index = this._outlet.indexOf(this.tab.contentViewRef);
    if (index !== -1) {
      this._outlet.detach(index);
    }
  }
}
